h1 {
  border: 1px solid red;
  text-align: center;
  font: bold 30px/20px arial;
}

.main {
  width: 99%;
  border: 1px solid green;
  display: flex;
}

.mapField {
  border: 1px solid red;
  position: relative;
  max-width: 60%;
}

.infoLegendField {
  display: flex;
  flex-direction: column;
  border: 1px solid blue;
  width: 40%;
}

.legend {
  padding-top: 4%;
  padding-left: 15%;
  height: 30%;
  border: 1px solid grey
}

.legendSpan {
  display:inline-block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.info {
  padding-top: 7%;
  padding-left: 15%;
  height: 70%;
}

.audio {
  display: flex;
  justify-content: center;
}

img {
  width: 100%;
}


/*Information block start*/

.information {
  position: relative;
  width: 50%;
  background-color: #fff;
  z-index:6;
  border: 10px solid rgb(166, 155, 195);
  border-radius: 25px;
  padding: 15px;
}

.infoClose {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 50px;
  height: 50px;
}

.infoClose span {
  display: block;
  position: absolute;
  border: 3px solid rgb(166, 155, 195);
  width: 25px;
}

.infoClose:hover span {
  border: 3px solid red;
}

.infoClose span:first-child{
  transform: rotate(45deg);
  margin: 20px 10px;
}

.infoClose span:last-child{
  transform: rotate(-45deg);
  margin: 20px 10px;
}

.infoTitle {
  text-align: center;
}

.infoTrans {
  text-align: center;
}

.infoNote, .infoPlus{
  text-align: justify;
  text-indent: 35px;
}

/*information block end*/

/*Style of points start*/

.villagesSpan{
  display: block;
  position: absolute;
  border: 10px solid black;
  border: black 10px solid;
  border-radius: 50%;
  z-index: 4;
  width: 3.5%;
  height: 4.5%;
}

.villagesSpan:hover {
  border: none;
  background-color: blue;
  z-index: 6;
  transform: scale(1.3);
  transform-origin: center;
}

.yellow {
  border: none;
  background-color: yellow;
  z-index: 5;
}

.red {
  border: none;
  background-color: red;
  z-index: 5;
}

.greenRed {
  background-color: red;
  z-index: 5;
  border: 5px green solid;
  box-sizing: border-box;
}

.greenYellow{
  background-color: yellow;
  z-index: 5;
  border: 5px green solid;
  box-sizing: border-box;
}

/*Style of points end*/
